.table {
  margin-bottom: 68px;
  border-collapse: separate; /* Используем separate для корректной работы border-radius */
  table-layout: fixed;
  border-spacing: 0;
  border-radius: 8px;
  overflow: hidden; /* Обрезаем внутреннее содержимое по радиусам */
  border: 1px solid rgba(29, 30, 33, 0.08);
  text-align: left;
}

@media only screen and (min-width: 1440px) {
  .table {
    margin-bottom: 0;
  }
}

.header {
  color: rgb(29, 30, 33);
  font-size: 16px;
  font-weight: 600;
  line-height: 1.25;

  border-bottom: 1px solid rgba(29, 30, 33, 0.08);

  background: rgb(231, 241, 237);
  padding: 14px;
  width: 315px;
}

.subheader {
  color: rgba(29, 30, 33, 0.4);
  font-size: 12px;
  font-weight: 500;
  line-height: 1.16;

  background: rgb(255, 255, 255);
  padding: 14px;
  border-right: 1px solid rgba(29, 30, 33, 0.08);
}

.row {
  padding: 10px;

  color: rgb(29, 30, 33);
  font-size: 12px;
  font-weight: 500;
  line-height: 1.16;
  background: rgb(255, 255, 255);
  border-right: 1px solid rgba(29, 30, 33, 0.08);
  border-top: 1px solid rgba(29, 30, 33, 0.08);
}

.cellWrap {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  gap: 8px;
}

@media only screen and (min-width: 768px) {
  .cellWrap {
    align-items: center;
    flex-direction: row;
  }

  .header,
  .subheader,
  .row {
    padding: 20px;
  }

  .header {
    font-size: 18px;
    width: 704px;
  }

  .row {
    font-size: 16px;
  }

  .subheader {
    font-size: 14px;
  }
}

@media only screen and (min-width: 1440px) {
  .cellWrap {
    flex-wrap: nowrap;
  }
}

.avatar {
  width: 24px;
  height: 24px;
  border-radius: 50%;
}

tr td:last-child,
tr th:last-child {
  border-right: none;
}

.col-name {
  max-width: 90px;
}

.col-email {
  max-width: 120px;
}

@media only screen and (min-width: 375px) {
  .col-name {
    max-width: 106px;
  }

  .col-email {
    max-width: 151px;
  }
}

/* .col-spent {
  min-width: 78px;
} */

.col-name,
.col-email,
.col-spent,
.cellWrap {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  position: relative;
}

@media only screen and (min-width: 768px) {
  .col-name {
    min-width: 255px;
  }

  .col-email {
    min-width: 279px;
  }
}

@media only screen and (min-width: 1440px) {
  .col-name {
    min-width: 223px;
  }
  .col-email {
    min-width: 269px;
  }
}
