.container {
  padding: 100px 20px 20px;
  min-width: 280px;
  max-width: 375px;
  /* margin: 0 auto; */
}

@media only screen and (min-width: 768px) {
  .container {
    padding: 100px 32px 32px 32px;
    max-width: 768px;
  }
}

@media only screen and (min-width: 1440px) {
  .container {
    padding: 100px 40px 40px 118px;
    max-width: 1440px;
  }
}

@media only screen and (min-width: 1440px) {
  .tables {
    display: flex;
    gap: 20px;
    align-items: stretch;
    height: 512px;
  }
}
